@import '@helpers/_variables';

.view-type {
  padding: $padding-sm * 0.8;

  color: $white;
  line-height: 10px;

  border: none;
  outline: none;
  background: none;

  cursor: pointer;

  &_active {
    padding: $padding-sm * 0.8;

    background: $light-black;
    border-radius: $border-radius-circle; }

  @media (hover) {
    &:hover {
      border-radius: $border-radius-circle;
      background: lighten( $light-black , 5% ); } } }
